* {
	box-sizing: border-box;
	margin: 0;
	padding: 0; }

$text_color: yellow;

@mixin size {
	width: 200px;
	height: 180px;
	text-align: center;
	display: block; }

.block {
	display: flex;
	justify-content: space-around;
	align-items: center;
	flex-wrap: wrap; }

.title {
	@include size;
	background-color: blue;
	color: $text_color; }

.title2 {
	@include size;
	background-color: red;
	color: $text_color; }

.title3 {
	@include size;
	background-color: black;
	color: $text_color; }
